import { getInstrumentation } from 'o11y/client';
import { PublicSafety, utility } from 'o11y/shared';
import { webVitalsSchema } from 'o11y_schema/sf_instrumentation';
import { onCLS, onFCP, onFID, onLCP, onTTFB, onINP, Metric } from 'web-vitals';

import { Instrumentation } from '../interfaces';
import { WebVitalsData } from '../interfaces-private/WebVitalsData';
import { webVitalsVersion } from '../version';

// Maintain order for backwards compatibility
export enum WebVitalsMetrics {
    CLS, // Cumulative Layout Shift https://web.dev/articles/cls/
    FCP, // First Contentful Paint https://web.dev/articles/fcp/
    FID, // First Input Delay https://web.dev/articles/fid/
    LCP, // Largest Contentul Paint https://web.dev/articles/lcp/
    TBT, // Total Blocking Time Lab https://web.dev/articles/tbt/ -- Lab only (no "getTBT()")
    TTFB, // Time to First Byte https://web.dev/articles/ttfb/
    INP // Interaction to Next Paint https://web.dev/articles/inp
}

// 1 = not reported if page was loaded in the background
// 2 = not reported if the user never interacts with the page

const defaultMetrics = [
    WebVitalsMetrics.CLS, // 1
    WebVitalsMetrics.FCP, // 1
    WebVitalsMetrics.FID, // 1, 2
    WebVitalsMetrics.LCP, // 1
    WebVitalsMetrics.TTFB,
    WebVitalsMetrics.INP // 2
];

class WebVitals {
    private _safety: PublicSafety;
    private _instr: Instrumentation;
    private _isInitialized = false;

    constructor() {
        this._safety = new PublicSafety();
        this._instr = getInstrumentation('WebVitals');
    }

    activate(/** @deprecated */ __instr?: Instrumentation, metrics?: WebVitalsMetrics[]): void {
        this._safety.tryCatch(() => {
            if (this._isInitialized) {
                throw new Error('WebVitals is already activated.');
            }
            utility.requireArgumentIfDefined(metrics, 'metrics', Array);
            this._isInitialized = true;

            const metricSet = new Set(metrics || defaultMetrics);
            if (metricSet.delete(WebVitalsMetrics.CLS)) {
                onCLS(this._logHandler.bind(this));
            }
            if (metricSet.delete(WebVitalsMetrics.FCP)) {
                onFCP(this._activityHandler.bind(this));
            }
            if (metricSet.delete(WebVitalsMetrics.FID)) {
                onFID(this._logHandler.bind(this));
            }
            if (metricSet.delete(WebVitalsMetrics.LCP)) {
                onLCP(this._activityHandler.bind(this));
            }
            if (metricSet.delete(WebVitalsMetrics.TTFB)) {
                onTTFB(this._activityHandler.bind(this));
            }
            if (metricSet.delete(WebVitalsMetrics.INP)) {
                onINP(this._activityHandler.bind(this));
            }
            if (metricSet.size > 0) {
                const unsupported = JSON.stringify(Array.from(metricSet.keys()));
                this._instr.error(`Unsupported WebVital metrics: ${unsupported}`);
            }
        });
    }

    private _getPayload(metric: Metric): WebVitalsData {
        return {
            name: metric.name,
            value: metric.value,
            delta: metric.delta,
            id: metric.id,
            navType: metric.navigationType,
            wvVersion: webVitalsVersion
        };
    }

    private _logHandler(metric: Metric): void {
        this._instr.log(webVitalsSchema, this._getPayload(metric));
    }

    private _activityHandler(metric: Metric): void {
        let perfStartOverride = 0;
        let perfStopOverride = metric.value;
        if (metric.name === 'INP') {
            const firstEntry = metric.entries[0] as PerformanceEntry;
            perfStartOverride += firstEntry.startTime;
            perfStopOverride += firstEntry.startTime;
        }

        this._instr.startActivity(metric.name).stop(webVitalsSchema, this._getPayload(metric), {
            perfStartOverride,
            perfStopOverride
        });
    }
}

export const webVitals = new WebVitals();
